.about-project {
    width: 100%;
    // height: 100vh;
    background: url('./components/MainPage/img/mainPageBac.jpg') no-repeat;
    background-size: cover;

    &__img-container{
        width: 40%;
        // height: 100vh;
    }
    
    &__container{   
        display: flex;
    }

    &__img{
       width: 100%;
       height: 100%;
       object-fit: cover;
    }
    
    &__text-part {
        width: 60%;
        padding: 0 60px 0 60px;
    }

    &__title {
        @include heading('Arkhip', 64px, 77px, $color-light-blue);
        float: right;
        margin: 30px 30px 0 0;
    }

    &__sub-title {
        @include heading('Arkhip', 48px, 35px, $color-black);
        text-align: center;
        margin: 300px 0 50px 0;
    }

    &__text {
        @include heading('Montserrat', 24px, 140%, $color-black);
        text-align: center;
        margin-bottom: 15px;
    }
}

@media (max-width: 878px) {
    .about-project {

		&__container {
		}

		&__img-container {
		}

		&__img {
		}

		&__text-part {
		}

		&__title {
            @include heading('Arkhip', 60px, 77px, $color-light-blue);
		}

		&__sub-title {
		}

		&__text {
		}
}

}

